import React from 'react';
import { Footer, Text, Box } from 'grommet';
import { Link } from 'react-router-dom'
import { Home, Tasks, Notification, Calendar } from 'grommet-icons';

export function BottomNav() {
    return (
    <Footer height='xxsmall' background='light-1' justify='around' pad='medium'>
        <Box align='center'>
            <Link to='/'>
                <Home size='medium'></Home>
            </Link>
            <Text size='xsmall'>Home</Text>
        </Box>
        <Box align='center'>
            <Link to='activities'>
                <Tasks size='medium'></Tasks>
            </Link>
            <Text size='xsmall'>Actividades</Text>
        </Box>
        <Box align='center'>
            <Link to=''>
                <Calendar size='medium'></Calendar>
            </Link>
            <Text size='xsmall'>Calendario</Text>
        </Box>
        <Box align='center'>
            <Link to=''>
                <Notification psize='medium'></Notification>
            </Link>
            <Text size='xsmall'>Alertas</Text>
        </Box>
    </Footer>

    );
}
