import { Heading, Card, CardHeader, CardBody, Button, CardFooter, Text, Tag } from 'grommet';
import moment  from 'moment';
import * as Icons from 'grommet-icons';

export function ActivityCard(props) {
    return (
            <Card height="small" margin={{bottom: 'medium'}}>
                <CardHeader background='light-2' pad="small">
                    <Text size='medium' weight={'lighter'} >{props.data.locationName}</Text>
                    <Tag background='neutral-2' size='small' name="" value={props.data.activityType} />
                </CardHeader>
                <CardBody pad="small">
                    <Text  weight={'lighter'}> Fecha: {moment(props.data.measureDate).format('DD-MM-YYYY HH:mm:ss')}</Text>
                </CardBody>
                <CardFooter justify='end' pad={{ horizontal: "small" }} background="light-2">
                    <Button icon={<Icons.Download color="dark-3" />} hoverIndicator />
                </CardFooter>
            </Card>
    );
}
