import React, { useState, useEffect } from 'react';
import { Box, Card, CardHeader, CardBody, Button, CardFooter, Heading, Text, Avatar } from 'grommet';
import { Link } from 'react-router-dom'
import * as Icons from 'grommet-icons';


export function Home() {
    return (
        <Box fill  pad={{top: 'large', left: 'medium', right: 'medium', bottom: 'large' }}>
            <Box pad='xlarge' align='center'>
                <Avatar size='xlarge' background="neutral-2">
                    <Icons.UserFemale size='large' color="light-2" />
                </Avatar>
                <Heading level={'1'}>¡Bienvenida!</Heading>
                <Text>María López</Text>
                <Text>Nodo A</Text>
            </Box>
            <Card background="light-1">
                <CardHeader align='center' pad="medium">
                    <Heading level='2'>Resumen</Heading>
                </CardHeader>
                <CardBody align='center' pad="medium">
                </CardBody>
                <CardFooter  justify='end' pad={{ horizontal: "small" }} background="light-5">
                    <Box row justify='around'>
                        <Button icon={<Icons.ShareOption color="dark-2" />} hoverIndicator />
                    </Box>
                </CardFooter>
            </Card>
        </Box>
    );
}
