import React, { useState, useEffect } from 'react';
import { Box, Heading, Button, Header} from 'grommet';
import { Link, Outlet } from 'react-router-dom'
import { Add, Filter } from 'grommet-icons';

export function MyFieldActivities() {
    return (
        <Box fill>
            <Header pad={{ left: 'medium', right: 'medium' }}>
                <Heading level="2" color={"dark-1"}>Mis Actividades</Heading>
                <Link to='new'>
                    <Add></Add>
                </Link>
            </Header>
            <Outlet />
        </Box>
    );
}
