import React, { useState, useEffect } from 'react';
import {Notification, Box, List, Text, Tag } from 'grommet';
import { FormEdit } from 'grommet-icons';
import { services } from '../../services';
import { ActivityCard } from '../ActivityCard/ActivityCard';

export function ActivityList() {
    const [activities, setActivities] = useState();
    const [error, setError] = useState();

    useEffect(() => {
        const fetchActivities = async () => {
            let dataFromApi = await services.fieldActivityRest.getActivityByUser('testUser');
            let db = await services.idb.open();
            await services.idb.addMany(db, dataFromApi);
            let dataFromDb = await services.idb.getAll(db);
            setActivities(dataFromDb);
        }
        const fetchActivitiesfromIdb = async () => {
            let db = await services.idb.open();
            let data = await services.idb.getAll(db);
            setActivities(data);
        }
        try {
            fetchActivities();
        } catch (e) {
            handleError(e)
        }
    }, []);

    function handleError(e) {
        setError(e);
    }

    return (
        <Box>
            {error && (
                <Notification
                    toast
                    title="Error"
                    message={'error'}
                    />)}
            <Box flex='grow' pad='medium'>
              {activities && (activities.map((item, index) => <ActivityCard key={index} data={item} />))}
            </Box>
        </Box>
    );
}
